import React from "react";
import { skipParams, route, debounce } from "../utils/index";
import { useClient } from "../provider/HookContext";

const useFetch = (url, props) => {
  let abort = new AbortController();
  const api = useClient();
  const [params, setParams] = React.useState({});
  const [data, setData] = React.useState({
    error: {},
    data: {},
    loading: false,
  });

  const setQuery = (value) => {
    debounce(() => setParams({ ...params, ...value }), 60);
  };

  const getData = async () => {
    try {
      setData((state) => ({ ...state, loading: true }));
      const { data } = await api.get(
        route(
          typeof url === "object" ? url[0] : url,
          typeof url === "object" ? url[1] : {}
        ),
        {
          ...props.config,
          signal: abort.signal,
          params: {
            ...props.defaultParams,
            ...skipParams(params),
          },
        }
      );
      setData((state) => ({ ...state, data: props.selector(data) }));
    } catch (error) {
      setData((state) => ({ ...state, error }));
    } finally {
      setData((state) => ({ ...state, loading: false }));
    }
  };

  React.useEffect(() => {
    getData();
    return () => {
      abort.abort();
    };
  }, [params, ...props.deps]);

  const memoize = React.useMemo(() => data.data, [data]);

  return {
    loading: data.loading,
    data: memoize,
    error: data.error,
    query: params,
    setQuery,
  };
};

export default useFetch;
