import React from "react";

const HookContext = React.createContext();

const HookProvider = (props) => {
  const { children, client } = props;
  return (
    <HookContext.Provider value={{ api: client }}>
      {children}
    </HookContext.Provider>
  );
};

const useClient = () => {
  const context = React.useContext(HookContext);
  if (context === undefined) {
    throw new Error("useClient must be used within a HookProvider");
  }
  return context.api;
};

export { HookProvider, useClient };
