import { useCallback, useMemo, useReducer, useState } from "react";
import {
  camelCaseToString,
  snackCaseToString,
  omit,
  pick,
  route,
} from "./../utils/index";
import * as yup from "yup";
import { useClient } from "../provider/HookContext";
const dataReducer = (initialState, action) => {
  switch (action.type) {
    case "set":
      return {
        ...initialState,
        ...action.payload,
      };
    case "merge":
      return {
        ...initialState,
        ...action.payload,
      };
    case "clear":
      return action.payload;
    default:
      return initialState;
  }
};

const useMutation = ({ defaultValue, ...props }) => {
  const api = useClient();
  const controller = new AbortController();
  const _validation = {
    attributes: undefined,
    ...props.validation,
  };

  const [errors, sendError] = useState({});
  const [processing, setProcessing] = useState(false);
  const [loading, setLoading] = useState(false);
  const [value, dispatch] = useReducer(dataReducer, defaultValue);

  const validate = useCallback(
    async (direct = undefined) => {
      let invalid = false;
      let keys = [];

      try {
        sendError((state) => ({
          ...(typeof direct === "object" ? { ...state, [direct.bag]: {} } : {}),
        }));

        if (typeof direct === "object") {
          keys = Object.keys(direct.schema(yup).fields);
          await direct.schema(yup).validate(value, { abortEarly: false });
        } else {
          if (props.schema) {
            keys = Object.keys(props.schema(yup).fields);
            await props.schema(yup).validate(value, { abortEarly: false });
          }
        }
      } catch (err) {
        const _errors = err.inner
          .filter((v) => keys.includes(v.path))
          .filter((v) => {
            const options = {
              on: ({}) => true,
              ...(!direct && _validation.attributes[v.path]),
            };
            return options.on(value);
          })
          .reduce((prev, curr) => {
            const message = curr.message.replace(/_/g, " ");
            const key = snackCaseToString(camelCaseToString(curr.path));
            return {
              ...prev,
              [curr.path]: message.replace(curr.path, key),
            };
          }, {});

        if (direct) {
          sendError((state) => ({
            ...state,
            [direct.bag]: _errors,
          }));
          invalid = true;
        } else {
          sendError(_errors);
          invalid = Object.keys(errors).length > 0;
        }
      }
      return invalid;
    },
    [value]
  );

  const soloValidation = useCallback(
    async (key) => {
      let valid = true;
      const options = {
        on: ({}) => true,
        ..._validation.attributes[key],
      };

      if (options.on(value)) {
        try {
          sendError((state) => ({ ...(key ? omit(state, [key]) : {}) }));
          await props
            .schema(yup)
            .validate({ [key]: value[key] }, { abortEarly: false });
        } catch (err) {
          const find = err.inner.find((find) => find.path === key);
          if (find) {
            const message = find.message.replace(/_/g, " ");
            const label = snackCaseToString(camelCaseToString(find.path));
            sendError((state) => ({
              ...state,
              [find.path]: message.replace(find.path, label),
            }));
            valid = false;
          }
        }
      }
      return valid;
    },
    [value]
  );

  const setData = (values) => {
    const key = Object.keys(values).at(0);
    sendError((state) => ({ ...(key ? omit(state, [key]) : state) }));
    if (key && props.format && props.format[key]) {
      dispatch({
        type: "set",
        payload: {
          [key]: props.format[key](values[key]),
        },
      });
    } else {
      dispatch({ type: "set", payload: values });
    }
  };

  const reformat = (cb) => {
    dispatch({ type: "set", payload: cb(value) });
  };

  const setError = (values) => {
    sendError((state) => ({ ...state, ...values }));
  };

  const merge = (values) => {
    Object.assign(value, values);
  };

  const post = async (url, options = undefined, method = "post") => {
    try {
      setProcessing(true);

      if (options.validation) {
        const _schema =
          typeof options.validation === "object"
            ? options.validation
            : undefined;
        if (await validate(_schema)) return;
      }

      // if (await serverValidation(server)) return;

      if (options.onBeforeSend) options.onBeforeSend();

      if (options.options && options.options.headers) {
        let type = options.options.headers["content-type"];

        if (type && type === "multipart/form-data") {
          const form = new FormData();
          Object.entries(value).forEach(([key, val]) => form.append(key, val));
          options.options.data = form;
        }
      }

      const resp = await api[method](
        route(
          typeof url === "object" ? url[0] : url,
          typeof url === "object" ? url[1] : {}
        ),
        options.useEmpty
          ? {}
          : (options.options && options.options.data) || value,
        {
          signal: controller.signal,
          ...options.options,
        }
      );

      if (options.onSuccess) options.onSuccess(resp.data, omit(resp, ["data"]));
    } catch (e) {
      if (options.onError) options.onError(e);
    } finally {
      if (options.onAlways) options.onAlways();
      setProcessing(false);
    }
  };

  const put = async (url, options) => {
    await post(url, options, "put");
  };

  const get = async (url, options = undefined, method = "get") => {
    try {
      setLoading(true);
      if (options.onBeforeSend) options.onBeforeSend();

      const resp = await api[method](
        route(
          typeof url === "object" ? url[0] : url,
          typeof url === "object" ? url[1] : {}
        ),
        {
          signal: controller.signal,
          ...options.options,
        }
      );

      if (options.onSuccess) options.onSuccess(resp.data, omit(resp, ["data"]));
    } catch (e) {
      if (options.onError) options.onError(e);
    } finally {
      if (options.onAlways) options.onAlways();
      setLoading(false);
    }
  };

  const destroy = async (url, options) => {
    await get(url, options, "delete");
  };

  const clearData = (except = undefined) => {
    dispatch({
      type: "clear",
      payload: except
        ? { ...defaultValue, ...pick(value, except) }
        : defaultValue,
    });
  };

  const clearError = () => {
    sendError({});
  };

  const error = (key) => {
    return key in Object(errors);
  };

  const message = (key) => {
    return errors[key] || "";
  };

  const isValid = () => {
    return Object.keys(errors).length === 0;
  };

  const cancel = () => {
    controller.abort();
  };

  const registerValidate = (key) => {
    if (_validation.attributes[key] && _validation.attributes[key].solo) {
      return {
        onBlur: async (event) => {
          await soloValidation(key);
        },
      };
    }
    return undefined;
  };

  const register = (key, defaultValue = "", options = undefined) => {
    const _options = {
      value: (event) => event.target.value,
      ...options,
    };

    const props = {
      name: key,
      value: value[key] || defaultValue,
      onChange: (event) => setData({ [key]: _options.value(event) }),
      ...registerValidate(key),
    };

    return props;
  };

  const data = useMemo(() => value, [value]);

  return {
    loading,
    processing,
    errors,
    data,
    isNewRecord: props.isNewRecord && props.isNewRecord(value),
    setData,
    setError,
    reformat,
    validate,
    post,
    put,
    destroy,
    get,
    clearData,
    clearError,
    error,
    message,
    isValid,
    cancel,
    register,
    merge,
  };
};

export default useMutation;
