const route = (url, arg) => {
  let path = url;
  if (arg && typeof arg === "object" && !!Object.keys(arg).length) {
    Object.entries(arg).forEach(
      ([param, value]) => (path = url.replace(`:${param}`, value))
    );
  }

  return path;
};

const skipParams = (obj) => {
  return Object.fromEntries(Object.entries(obj).filter(([k, v]) => !!v));
};

const debounce = (func, timeout = 300) => {
  let timer;
  return () => {
    clearTimeout(timer);
    timer = setTimeout(() => {
      func();
    }, timeout);
  };
};

const pick = (obj, array) => {
  const newObj = {};
  Object.assign(newObj, ...array.map((key) => ({ [key]: obj[key] })));
  return newObj;
};

const omit = (obj, keys) => {
  if (!keys.length) return obj;
  const { [keys.pop()]: omitted, ...rest } = obj;
  return omit(rest, keys);
};

const snackCaseToString = (text) => {
  return text
    .split("_")
    .map((v) => ucword(v))
    .join(" ");
};

const camelCaseToString = (text) => {
  if (text) {
    const result = text.replace(/([A-Z])/g, " $1");
    return result[0].toUpperCase() + result.substring(1).toLowerCase();
  }
  return "";
};

export {
  route,
  skipParams,
  debounce,
  omit,
  pick,
  snackCaseToString,
  camelCaseToString,
};
